.reviews {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border-top: 1px solid var(--dark-purple);
}
.reviews div {
  max-width: 100%;
  padding: 10px;
  margin: 0 10px 0 10px;
}
.reviews div:nth-of-type(2n) {
  background-color: rgb(223, 223, 223);
}
.reviews .no-info {
  margin: 20px auto;
  color: var(--red);
}
